import React from 'react';
import GoogleLocationImport from './google-import';
import credentials from '../lib/credentials';
import DetectionServiceList from './detectionServiceList';
import { connect } from 'react-redux';
import { ButtonItem, MenuGroup, Section } from '@atlaskit/menu';
import '../style/settings.less';

const NoPage = () => {
    return <div>Empty settings pages</div>;
};

const classObj = (style) => Object.entries(style)
    .filter(([k,v]) => v)
    .map(([k, v]) => k)
    .join(' ');

export const FakeButton = props => {
    const styleFn = currentStyles => ({
        ...currentStyles,
        '&: hover': {
            'backgroundColor': 'transparent'
        }
    });
    return <ButtonItem elemBefore={props.elemBefore} cssFn={styleFn}>{props.children}</ButtonItem>;
};

//TODO: use react routing for sub-routes.
const Settings = (params) => {
    const [page, showPage] = React.useState(<NoPage/>);
    const [subtitle, setSubTitle] = React.useState(null);

    let openrouteInput = React.createRef();

    React.useEffect(() => {
        const getAPIKey = async () => {
            openrouteInput.current.value = await credentials.getKey('openroute') || '';
        };
        getAPIKey();
    },[]);

    const hidePage = () => {
        setSubTitle(null);
    };

    const selectPage = (title, component) => {
        showPage(component);
        setSubTitle(title);
    };

    const BackButton = () => {
        if(subtitle !== null){
            return <a href="#settings" className="back-link" onClick={hidePage}>
                <i className="m-i">navigate_before</i>
                Back
            </a>;
        }
        return <a href="#history" className="back-link">
            <i className="m-i">navigate_before</i>
            Back
        </a>;
    };

    let timeout;
    const onOpenRouteChange = (evt) => {
        const val = openrouteInput.current.value.trim();
        if(timeout){ clearInterval(timeout); }
        timeout = setTimeout(async () => {
            await credentials.setKey('openroute', val);
        }, 500);
    };

    return (
        <div className="page settings">
          <div className="header">
            <BackButton/>
            <h1>Settings</h1>
          </div>
          <div className={classObj({
                   "settings-container": true, 
                   "expanded": subtitle !== null
			   })}>
            <MenuGroup className="menu">
			  <Section title="Detection Services">
				<DetectionServiceList/>
			  </Section>
              <Section title="API Keys">
                <FakeButton elemBefore={<i className="m-i">directions</i>}>
                  <span>Openroute</span> 
                  <input ref={openrouteInput} type="text" onChange={onOpenRouteChange}></input>
                </FakeButton>
              </Section>
              <Section title="Import">
                <ButtonItem elemBefore={<i className="m-i">history</i>} onClick={() => selectPage("Google Import", <GoogleLocationImport/>)}>
        Import Google Location history
        </ButtonItem>
        <ButtonItem elemBefore={<i className="m-i">publish</i>}>Import DPP file. Coming soon</ButtonItem>
        </Section>
        <Section title="Export">
        <ButtonItem elemBefore={<i className="m-i">get_app</i>}>Export</ButtonItem>
        </Section>
        <Section title="About">
        <p>A short description of the DPP project.<a href="#">More details</a></p>
        <a href="#">Privacy Policy</a>
        </Section>
        </MenuGroup>
        <div id="settings-page-root">
        {page}
        </div>
        </div>
        </div>
    );
};

const connectState = (state) => ({

});

const connectDispatch = (dispatch) => ({

});

export default connect(connectState, connectDispatch)(Settings);
