import db from './store';
import location from './location';
import openroute from './openroute';
import device from './devices';
import L from 'leaflet';

const IX_VALID_EDGE = 'intersection-valid-edge-left';
let sharedIntersectionWorker;
function getIntersectionWorker(){
    
	if(!sharedIntersectionWorker){
		sharedIntersectionWorker = new Worker('../workers/intersection.worker.js', {type: 'module'});
	}
	return sharedIntersectionWorker;
}

function computeIntersections(entries, devices){
	if(!entries || entries.length === 0 || !devices || devices.length === 0){ 
		return [];
	}

	return new Promise((resolve, reject) => {
		const intersectionWorker = getIntersectionWorker();
		const channel = new MessageChannel();
		const port1 = channel.port1;
		const port2 = channel.port2;
		port1.onmessage = msg => {
			if(msg.data.event === "done"){
				resolve(msg.data.entries);
				port1.close();
			}
		}
		intersectionWorker.onerror = err => reject(err);
		intersectionWorker.postMessage({entries, devices, port: port2}, [port2]);
	});
}

//TODO: rename to update history
async function updateIntersections(fromMs, toMs){    
    console.log('computing');
    //get location history
    const locations = await location.getLocationHistory(fromMs, toMs);
    console.log('updateIx: locations', locations);

    //get openroute
    const openroutes = await getEstimatedPath(locations);
    console.log('updateIx: openroutes', openroutes);
    if(openroutes.length < 1){
        return {intersections: [], devices: [], openroutes: [], bounds: [], locations: []};
    }

    //get bounds for devices
    const latLonPath = openroutes.map(({latitude, longitude}) => [latitude, longitude]);
    const bounds = L.polyline(latLonPath).getBounds().pad(0.1);
    const boundsArr = [bounds.getSouthWest(), bounds.getNorthEast()].map(({lat, lng}) => [lat, lng]);

    const deviceBounds = L.latLngBounds(bounds);
    const minBounds = deviceBounds.getCenter().toBounds(5000);
    deviceBounds.extend(minBounds);   

    //get devices
    const deviceBoundsArr = [deviceBounds.getSouthWest(), deviceBounds.getNorthEast()].map(({lat, lng}) => [lat, lng]);
    const devices = await device.getDevicesAround(deviceBoundsArr);
    
    //compute intersections
    const intersections = await computeIntersections(openroutes, devices);
    await db.intersection.bulkPut(intersections);

    //store openroute
    await db.openroute.where('timestampMs').between(fromMs, toMs).delete();
    await db.openroute.bulkPut(openroutes);
    
    //update IX_VALID_EDGE
    await db.meta.put({id: IX_VALID_EDGE, value: fromMs});

    return { intersections, devices, openroutes, bounds: boundsArr, locations};
}

//get history between from and to
async function get(fromMs, toMs){
    const ixValidEdgeObj = (await db.meta.toArray()).find(m => m.id === IX_VALID_EDGE);
    let ixValidEdge = (ixValidEdgeObj && ixValidEdgeObj.value) || Date.now(); 
    console.log('ixValidEdge', ixValidEdge, fromMs);
    //Note: true is there to skip retrieval from db since the ixValidEdge check is not correct
    //TODO: find way to check if need to recompute
    if(true || ixValidEdge > fromMs){
        //if invalidated, update and return
        return updateIntersections(fromMs, toMs);
    }
    console.log('retrieving from db');
    //if not invalidated, we can just retrieve everything from local dbs
    const intersections = await db.intersection.where('timestampMs').between(fromMs, toMs).toArray();
    const locations = await location.getLocationHistory(fromMs, toMs);
    const openroutes = await db.openroute.where('timestampMs').between(fromMs, toMs).toArray();
    const path = openroutes.map(({latitude, longitude}) => [latitude, longitude]); 
    const bounds = L.polyline(path).getBounds().pad(1);
    const minBounds = bounds.getCenter().toBounds(5000);
    bounds.extend(minBounds);
    const boundsArr = [bounds.getSouthWest(), bounds.getNorthEast()].map(({lat, lng}) => [lat, lng]);

    const devices = await device.getDevicesAround(boundsArr);
    console.log('returning from intersections',{ intersections, devices, openroutes, bounds: boundsArr, locations});
    return { intersections, devices, openroutes, bounds: boundsArr, locations};
}

//TODO: there seems to be a bug with time estimation... need to investigate
function estimateTimestampsNSquared(recorded, estimated){
	//this is not euclidean distance, but it's just good enough to determine _relative_ "likeness"
	const distance = (a, b) => (a.latitude - b.latitude) * (a.latitude - b.latitude) 
							+ (a.longitude - b.longitude) * (a.longitude - b.longitude);
	let r = 1;
	let l = r;
	let min = 200;
	estimated[0].timestampMs = recorded[0].timestampMs;
	for(let i = 1, n = recorded.length; i < n; i++){
		min = 200;
		for(let j = r, m = estimated.length; j < m; j++){
			let dist = distance(recorded[i], estimated[j]);
			if(dist < min){
				min = dist;
				r = j;
			}
		}
		interpolate(estimated, l, r, recorded[i-1].timestampMs, recorded[i].timestampMs);
		l = r;
	}
	return estimated;
}

//BORKED, do not use
function matchEntries(recorded, estimated){
	const distance = (a, b) => (a.latitude - b.latitude) * (a.latitude - b.latitude) +
								(a.longitude - b.longitude) * (a.longitude - b.longitude);

	let eL = 1;
	let eR = estimated.length-2;
	let rL = 1;
	let rR = recorded.length-2;

	let minDistanceL = 200;
	let minDistanceR = 200;

	let lastL = eL;
	let lastR = eR;

	while(eL < eR && rL < rR){
		let distanceL = distance(estimated[eL], recorded[rL]);
		if(distanceL > minDistanceL){
			interpolate(estimated, lastL, eL, recorded[rL].timestamp, recorded[rL-1].timestamp);
			rL++;
			lastL = eL;
			minDistanceL = 200;
		}else{
			eL++;
			minDistanceL = distanceL;
		}
		let distanceR = distance(estimated[eR], recorded[rR]);
		if(distanceR > minDistanceR){
			interpolate(estimated, eR, lastR, recorded[rR].timestamp, recorded[rR+1].timestamp);
			rR--;
			lastR = eR;
			minDistanceR = 200;
		}else{
			eR--;
			minDistanceR = distanceR;
		}
	}
}

function estimateTime(recorded, estimated){
	return estimateTimestampsNSquared(recorded, estimated);
}

async function getEstimatedPath(entries){
	if(!entries || entries.length === 0){
		console.warn("getEstimatedPath entries length 0."); 
		return []; 
	}
	const coordinates = entries.map(entry => ([entry.longitude, entry.latitude]));
	const estimated = (await openroute.estimatePath(coordinates))
		.map(coord => ({longitude: coord[0], latitude: coord[1]}));
	estimateTime(entries, estimated);
	return estimated;
}

function interpolate(entries, l, r, t1, t2){
	const n = r - l || 1;
	const dt = t2 - t1;
	let Ti = dt/n;
	for(let i = l; i <= r; i++){
		entries[i].timestampMs = t1 + Ti * ((i-l) || 1);
	}
}


export default { get };