import NativeInterface from './nativeInterface.js';
import location from '../../lib/location';
import { groupBy, uuidv4 } from '../../lib/util';


export default class AndroidInterface{
	constructor(){
		window.AndroidInterface = this;
		this.callbackMap = {};
	}

	reply(message, { data, error }){
		return { id: message.id, data, error };
	}

	handleReply(message){
		const { id, data, error } = message;
		const cb = this.callback[id];
		
		data && cb.resolve(data) || error && cb.reject(error);
		delete this.callbackMap[message.id];
	}
	
	async receive(message){ //from native
		try{
			if(this.callbackMap[message.id]){
				return this.handleReply(message);
			}
			this.send(this.reply(message, { data: await this.handleMessage(message) }));
			return;
		}catch(error){
			this.send(this.reply(message, { error }));
		}
	}

	send(message){ //to native
		window.Android.sendMessage(message);
		return Promise.race([
			new Promise((resolve, reject) => { this.callbackMap[message.id] = { resolve, reject }; }),
			new Promise((resolve, reject) => { setTimeout(() => reject({ error:"Timeout" }), 5000); })
		]);
	}

	setMessageHandler(handler){
		this.handleMessage = handler;
	}
	
}



