import React from 'react';
import {
    useRouteMatch,
    HashRouter,
    Route,
    Link,
    Redirect,
    useHistory,
    Switch
} from 'react-router-dom';
import Current from "./current";
import Settings from "./settings";
import History from "./history";
import { Menu, Icon } from 'semantic-ui-react';

const CustomMenuItem = ({ to, children }) => {
    const match = useRouteMatch({
        exact: false,
        path: to
    });
    const history = useHistory();

    const goToRoute = () => {
        history.push(to);
    };
    
    return (
        <Menu.Item active={match !== null} onClick={goToRoute}>
          {children}
        </Menu.Item>
    );
}

const Main = () => {
    const defaultDate = new Date().toISOString().split('T')[0];
    
	return (
        <HashRouter>
          <Switch>
            <Redirect exact from="/history" to={`/history/${defaultDate}`}/>
            <Route path="/history/:date">
              <History/>
            </Route>
            <Route path="/settings">
              <Settings/>
            </Route>
            <Route path="/">
              <Current/>
            </Route>     
          </Switch>
          <div className="menu">
            <Menu widths={3} fluid icon='labeled'>
              <CustomMenuItem to="/current">
                <Icon name="gamepad"/>
                Current
              </CustomMenuItem>        
              <CustomMenuItem to="/history">
                <Icon name="gamepad"/>
                History
              </CustomMenuItem>        
              <CustomMenuItem to="/settings">
                <Icon name="gamepad"/>
                Settings
              </CustomMenuItem>        
           </Menu>
          </div>
        </HashRouter>
    );
};

export default Main;
