import React from 'react';
import IntersectionList from './intersection-list';

const NoIntersections = () => {
    return <div className="no-intersections">No intersections</div>
}

const CurrentIntersections = ({intersections}) => {
    return(
        <div className="current-intersections">
            {   
            intersections.length > 0 ? 
                <IntersectionList intersections={intersections}/> : 
                <NoIntersections/>
            }
        </div>
    );
}

export default CurrentIntersections;