import { Evented, groupBy } from '../util.js';

import location from '../location.js';

const eventHandlers = {
	location: locationUpdate,
	
}

function locationUpdate(events){
	const MIN_ACCURACY = 50; //meters
	
	let locations = events.filter(evt => evt.data.accuracy < MIN_ACCURACY)
		.map(evt => ({...evt.data, timestamp: evt.timestamp}));
	return location.newBulkPut(locations);
}

function UDPHandler(events){}

function defaultEventHandler(event){
	console.log('no event handler found for event of type', event.type);
}

function handleMessage({ type, data }){
	if(!type){
		return;
	}
	if(type === 'event'){
		return handleEvent(data);
	}
	throw "message type unknown";
}

function handleEvent(data){
	return Promise.all(
		Object.values(groupBy(data, "type"))
			.map(([type, entries]) => eventHandlers[type](entries))
	)
}

const nativeInterface = native => {
	const sendMessage = (type, data) => native.send({type, data});
	native.setMessageHandler(handleMessage);
	sendMessage('webview_ready');

	return {
		test: () => sendMessage('test'),
		getServices: () => sendMessage("get_detection_services"),
		settings:{
			toggleService: id => sendMessage("toggle_detection_service", { id })
		}
	}
}


export default nativeInterface;
