import React from 'react';
import { connect } from 'react-redux';
import CurrentIntersections from '../component/current-intersections';
import RecentIntersections from '../component/recent-intersections';

/**
 * Main container shows currently intersecting controllers and the most recent
 * intersections.
 *  
 */

const Current = ({ recentIntersections, currentIntersections }) => {
    return (
		<div className="main page">
            <CurrentIntersections intersections={currentIntersections}/>
        </div>
    );
}

const connectState = (state) => {
    return {
        currentIntersections: [],
        recentIntersections: []
    };
};

export default connect(connectState)(Current);
