import React from 'react';
import '../style/intersection-card.css';
import Panel from '@atlaskit/panel';

const timeFormatter = Intl.DateTimeFormat('en-GB', {
    hour: 'numeric',
    minute: 'numeric'
});

const TimeLabels = ({start, end}) => {
    if(!start && !end){
        return;
    }

    let startTimeLabel = start && timeFormatter.format(new Date(start));
    let endTimeLabel = end && timeFormatter.format(new Date(end));
    startTimeLabel = startTimeLabel || endTimeLabel;
    endTimeLabel = endTimeLabel || startTimeLabel;

    if(startTimeLabel === endTimeLabel){
        return <div className = "times">{startTimeLabel}</div>
    }

    return <div className = "times">{startTimeLabel} - {endTimeLabel}</div>
}


const IntersectionCard = ({intersection, expanded = false}) => {
    

    const Header = (pManifest) => (
        <span className="title">
            { pManifest.name || getGenericName(pManifest) }
        </span>
    );

    const getDetectionType = (intersection) => {
        if(intersection.detectionType === 'geolocation'){
            return "ðŸŒ Geolocation";
        }
        return;
    }
    const getGenericName = (pManifest) => {
        if(pManifest['man_made']){
            const manMade = pManifest['man_made'];
            if(manMade === 'surveillance'){
                return "ðŸ‘ï¸ Surveillance device"
            }
        }
        return 'Unnamed device';
    }
  
    const Description = (pManifest) => {
       return  Object.keys(pManifest).map(k => {
            let value = pManifest[k];
            if(Array.isArray(value)){
                value = value.join();
            }
            return <div key={k}>{k}: {value}</div>
        })
    }

    return (
        <div className="intersection-card">
            <div className="inner">
                <div className="manifest-details">
                    <TimeLabels start={intersection.startMs} end={intersection.endMs}/>
                    <Panel header={Header(intersection.device.privacy_manifest)}>
                        <div className="description">
                            {Description(intersection.device.privacy_manifest)}
                        </div>
                    </Panel>
                </div>
                <div className="bottom">
                    <div className="provenance">{getDetectionType(intersection)}</div>
                </div>
            </div>
        </div>
    );
}

export default IntersectionCard;