import { SELECT_DATE } from '../actions/history.js';
import { UPDATE_HISTORY_ENTRIES } from '../actions/history';

const defaultState = {
    selectedDateISO: new Date().toISOString(),
    entries:{
        locations: [],
        devices: [],
        intersections: [],
        bounds: [],
        openroutes: []
    }
}

const historyReducer = (state = defaultState, action) => {
    console.log('called reducer', state, action);
    switch (action.type){
        case SELECT_DATE:
            return {
                ...state,
                selectedDateISO: action.selectedDateISO
            }
        case UPDATE_HISTORY_ENTRIES:
            return {
                ...state,
                entries: action.entries
            }
        default:
            return state;
    }
}

export default historyReducer;